.card{
    padding: 10px;
    box-shadow: 0 0 5px #ffdfdf;
    border-radius: 5px;
    overflow: hidden;
    max-width: 700px;
    width: 100%;
    justify-content: center;
}

.card .p{
    font-weight: bold;
    color: #0086fe;
}

.card button{
outline: 0;
border: 0;
color: #fff;
border-radius: 4px;
cursor: pointer;
font-weight: 400;
padding: 10px 13px;
width: 100%;
background: #0086fe;
}

.card .drag-area{
    height: 150 px;
    border-radius: 5px;
    border: 2px dashed #0086fe;
    color: #0086fe;
    background: #f4f3f9;
    display:flex;
    justify-content: center;
    align-items: center;
    user-select: none;
    -webkit-user-select: none;
    margin-top: 10px;
    padding: 100px 13px;
}

.card .drag-area .visible{
    font-size: 18px;
}

.card .select{
    color: #5256ad;
    margin-left: 5px;
    cursor: pointer;
    transition: 0.4s;
}

.card .select:hover{
    opacity: 0.6;
}

.card .container{
    width: 100%;
    height: auto;
    display: flex;
    justify-content: center;
    align-items: flex-start;
    flex-wrap: wrap;
    max-height: 200px;
    overflow-y: auto;
    margin-top: 10px;
    border: 2px;
}

.card .container .image{
    width: 75px;
    margin-right: 5px;
    height: 75px;
    position: relative;
    margin-bottom: 8px;
}

.card .container .image img{
    width: 100%;
    height: 100%;
    border-radius: 5px;
}

.card .container .image span{
    position: absolute;
    top: -2px;
    right: 9px;
    font-size: 20px;
    cursor: pointer;
}

.card input, .card .drag-area .on-drop,
.card .drag-area.dragover .visible{
    display: none;
}

.delete{
    z-index:999p;
    color: #ffffff;
}

